  /* Font faces */
  --serif: Iowan Old Style, Apple Garamond, Baskerville, Times New Roman,
    Droid Serif, Times, Source Serif Pro, serif, Apple Color Emoji,
    Segoe UI Emoji, Segoe UI Symbol;
  --sans-serif: 'Nunito', -apple-system, BlinkMacSystemFont, avenir next, avenir,
    segoe ui, helvetica neue, helvetica, Cantarell, Ubuntu, roboto, noto, arial,
    sans-serif;
  --logotype: 'Titillium Web', -apple-system, BlinkMacSystemFont, avenir next,
    avenir, segoe ui, helvetica neue, helvetica, Cantarell, Ubuntu, roboto, noto,
    arial, sans-serif;

  font: 1rem var(--sans-serif);
  font-weight: 400;
  color: var(--color-dark1);

  /* Font sizes */
  --text-xs: 0.75rem;
  --text-sm: 0.875rem;
  --text-base: 1rem;
  --text-lg: 1.125rem;
  --text-xl: 1.25rem;
  --text-xxl: 1.5rem;
  --text-xxxl: 2rem;
  --text-xxxxl: 2.5rem;

  /* Spacing */
  --space-xxxs: 0.25rem;
  --space-xxs: 0.375rem;
  --space-xs: 0.5rem;
  --space-sm: 0.75rem;
  --space-md: 1rem;
  --space-lg: 1.5rem;
  --space-xl: 2rem;
  --space-xxl: 2.5rem;
  --space-xxxl: 3rem;
  --space-xxxxl: 4rem;

  /* Colors */
  --color-main-bg: #ffffff;
  --color-light1: #ffffff;
  --color-dark1: #001c29;
  --color-accent1: #01608c;
  --color-accent2: #07a6e0;
  --color-accent3: #72d4f7;

  /* Drop shadow */
  --drop-shadow-md: drop-shadow(3px 3px 2px #e5e5e5);

  /* Border radius */
  --border-radius-sm: 3px;
  --border-radius-lg: 9px;

  background-size: cover;
  margin: var(--space-sm);
}

header {
  margin-block: 1rem;
}

.logo-wrapper {
  display: grid;
  grid-auto-flow: column;
  justify-content: center;
  align-items: center;
}

.logo-image {
  width: 48px;
}

.logo-name {
  font-family: var(--logotype);
  font-size: var(--text-xxxl);
  font-style: italic;
  font-weight: 700;
  display: inline-block;
}

.main {
  /* display: grid; */
  /* justify-content: center; */
  max-width: 600px;
  margin: 0 auto;
}

.form {
  display: flex;
  gap: var(--space-sm);
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  margin-bottom: var(--space-xxl);
}

#input-city {
  border-radius: var(--border-radius-sm);
  border: 1px solid var(--color-accent2);
  padding-inline: var(--space-xs);
  padding-block: var(--space-xxs);
  font-weight: 600;
  text-transform: uppercase;
}

#input-city::placeholder {
  color: var(--color-accent2);
}

#button-city {
  min-height: 3rem;
  color: var(--color-light1);
  letter-spacing: 1px;
  cursor: pointer;
  background: var(--color-accent1);
  border: 1px solid rgba(27, 31, 35, 0.15);
  border-radius: var(--border-radius-sm);
  box-shadow: rgba(27, 31, 35, 0.1) 0 1px 0;
  padding-inline: var(--space-lg);
}

:is(input, button):focus {
  outline: 3px dashed var(--color-accent2);
}

.display {
  color: white;
  text-shadow: 1px 1px 4px black;
}

.section-top {
  display: grid;
  justify-content: center;
  text-align: center;
  font-size: var(--text-xxl);
  margin-bottom: var(--space-xxl);
}

.city {
  font-size: 3rem;
}

.temp {
  font-size: 5rem;
  font-weight: 200;
  padding: 0;
  margin: 0;
  line-height: 4.5rem;
}

.conditions {
}

.high-low-wrapper {
  display: grid;
  grid-auto-flow: column;
  justify-content: center;
  gap: var(--space-md);
}

.high {
}

.low {
}

.section-middle {
  border-radius: var(--border-radius-lg);
  backdrop-filter: blur(10px) brightness(90%);
  margin-bottom: var(--space-xxxl);
}

.description {
  padding: var(--space-sm);
  border-bottom: 1px solid hsla(0, 0%, 100%, 25%);
}

.hourly-forecast-wrapper {
  padding: var(--space-xs);
  display: grid;
  gap: var(--space-xs);
  grid-template-columns: repeat(auto-fit, minmax(45px, 1fr));
}

.one-hour-wrapper {
}

.one-hour-wrapper {
  font-size: var(--text-sm);
  text-align: center;
}

.one-hour-precipprob {
  font-size: var(--text-xs);
  color: gainsboro;
}

.section-bottom {
  border-radius: var(--border-radius-lg);
  backdrop-filter: blur(10px) brightness(90%);
}

.extended-forecast-label {
  padding: var(--space-sm);
  border-bottom: 1px solid hsla(0, 0%, 100%, 25%);
}

.extended-forecast-wrapper {
  display: grid;
  grid-auto-flow: row;
  padding: var(--space-sm);
}

.one-day-wrapper {
  display: grid;
  gap: var(--space-xxs);
  grid-auto-flow: column;
  grid-auto-columns: 1fr;
  justify-items: center;
  border-bottom: 1px solid hsla(0, 0%, 100%, 25%);
}

.one-day-wrapper:last-child {
  border-bottom: none;
}

.one-day-label {
  display: grid;
  /* justify-self: start; */
  align-items: center;
}

.one-day-icon-wrapper {
  display: grid;
  justify-content: center;
  /* outline: 3px dashed lime; */
}

.sunny,
.clear-day,
.clear-night,
.wind {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}

.rain,
.rain-snow-showers-day,
.rain-snow-showers-night,
.rain-snow,
.rain,
.showers-day,
.showers-night,
.sleet {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
}

.cloudy,
.fog,
.hail {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
}

.partly-cloudy-day,
.partly-cloudy-night {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_3___});
}

.snow-showers-day,
.snow-showers-night,
.snow {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_4___});
}

.thunder-rain,
.thunder-showers-day,
.thunder-showers-night,
.thunder {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_5___});
}

@media (min-width: 340px) {
  .logo-name {
    font-size: 2.5rem;
  }
}
